import styled from 'styled-components'
import { Link,useLocation } from 'react-router-dom'
import classNames from 'classnames';
import Label from '../Label/Label';

const Wrapper = styled.div`
width: 795px;
display: flex;
justify-content: space-around;
margin-left: 14px;
.menu-item {
  position : relative;
  .beta-icon {
    position : absolute;
    top: -14px;
    left: 6px;
  }
  a {
    color : #93A1B8;
    text-decoration: none;
  }
}
.menu-item.selected a{
  color : #E0ECFF;
}
`
export default function Menu({lang}){
  const location = useLocation();

  const menus = [
    {text : lang['pools'],link : '/pool'},
    {text : lang['Futures'],link : '/trade/futures'},
    {text : lang['options'],link : '/trade/options',icon : <Label text={lang['beta']} height='16' borderRadius = '10px' bgColor='#CD7A37' color='#FFFFFF' fontSize='11' className='beta-icon'/>},
    {text : lang['token'],link : '/token'},
    {text : lang['bridge'],link : '/bridge'},
    {text : lang['docs'],subMenu : [] ,link : ''},
    {text : lang['more'],subMenu : [],link : ''},
    {text : lang['trade-to-earn'],icon : '',link : '/trade-to-earn'}
  ]

  return (
    <Wrapper>
      {menus.map((menu,index) => {
        const clazz = classNames('menu-item',{selected : location.pathname === menu.link})
        return(
          <span className={clazz} key={index}>
            {menu.icon}
            <Link to={menu.link}>{menu.text}</Link>
          </span>
        )
      })}
    </Wrapper>
  ) 
}